/* eslint-disable react-hooks/exhaustive-deps */
import React, {useState, useEffect} from 'react';
import {
  SafeAreaView,
  TouchableOpacity,
  Text,
  ScrollView,
  View,
  Image,
  TextInput,
  Picker,
  FlatList,
} from 'react-native';
import {logo_long, search, back, filter, minus, profil} from '../../assets';
import {GetProduk, GetProfile, URLPHOTO} from '../../config/api';
import {Yellow, White, Black, Grey} from '../../styles/style';
import {WARNA_PUTIH} from '../../utils/constant';
import {formatRupiah} from '../../utils/GlobalFunctions';
import {useIsFocused} from '@react-navigation/native';
import AsyncStorage from '@react-native-async-storage/async-storage';

const AkunSaya = ({navigation}) => {
  const isFocused = useIsFocused();
  const [Id, setId] = useState('');
  const [Nama, setNama] = useState('');
  const [Email, setEmail] = useState('');
  const [TglLahir, setTglLahir] = useState('');
  const [JenisK, setJenisK] = useState('');
  const [NoHp, setNoHp] = useState('');
  const [Alamat, setAlamat] = useState('');
  const [Photo, setPhoto] = useState('');
  const getProfil = async () => {
    let profile = await AsyncStorage.getItem('profile');
    if (profile != null) {
      let id = JSON.parse(profile).id;
      await GetProfile(id).then((res) => {
        if (res.data.err_code == '00') {
          let datap = res.data.data;
          setNama(datap.nama);
          setId(datap.id);
          setEmail(datap.email);
          setTglLahir(datap.tgl_lahir);
          setJenisK(datap.jenis_kelamin);
          setNoHp(datap.nohp);
          setAlamat(datap.alamat);
          setPhoto(datap.photo);
        }
      });
    }
  };

  useEffect(() => {
    console.log('AKUN SAYA');
    getProfil();
  }, [isFocused]);

  return (
    <SafeAreaView style={{flex: 1, backgroundColor: WARNA_PUTIH}}>
      <ScrollView>
        <View
          style={{
            flex: 1,
            // position: 'absolute',
            paddingTop: 64,
            width: '100%',
            alignItems: 'center',
            padding: 10,
          }}>
          <Image
            source={
              Photo == null || Photo == '' || Photo == undefined
                ? profil
                : {uri: URLPHOTO + Photo}
            }
            style={{width: 200, height: 200}}
            resizeMethod="resize"
            resizeMode="contain"
          />
          <Text style={{fontSize: 20, fontWeight: 'bold'}}>AKU SAYA</Text>
          <Text
            style={{fontSize: 15, fontWeight: 'bold', alignSelf: 'flex-start'}}>
            Nama :
          </Text>
          <TextInput
            style={{
              borderWidth: 1,
              width: '100%',
              fontSize: 15,
              borderRadius: 10,
              marginBottom: 5,
            }}
            value={Nama}
          />
          <Text
            style={{fontSize: 15, fontWeight: 'bold', alignSelf: 'flex-start'}}>
            No HP :
          </Text>
          <TextInput
            style={{
              borderWidth: 1,
              width: '100%',
              fontSize: 15,
              borderRadius: 10,
              marginBottom: 5,
            }}
            value={NoHp}
          />
          <Text
            style={{fontSize: 15, fontWeight: 'bold', alignSelf: 'flex-start'}}>
            Email :
          </Text>
          <TextInput
            style={{
              borderWidth: 1,
              width: '100%',
              fontSize: 15,
              borderRadius: 10,
              marginBottom: 5,
            }}
            value={Email}
            editable={false}
          />
          <Text
            style={{fontSize: 15, fontWeight: 'bold', alignSelf: 'flex-start'}}>
            Jenis Kelamin :
          </Text>
          <TextInput
            style={{
              borderWidth: 1,
              width: '100%',
              fontSize: 15,
              borderRadius: 10,
              marginBottom: 5,
            }}
            value={JenisK}
          />
          <Text
            style={{fontSize: 15, fontWeight: 'bold', alignSelf: 'flex-start'}}>
            Tanggal Lahir :
          </Text>
          <TextInput
            style={{
              borderWidth: 1,
              width: '100%',
              fontSize: 15,
              borderRadius: 10,
              marginBottom: 5,
            }}
            value={TglLahir}
          />
          <Text
            style={{fontSize: 15, fontWeight: 'bold', alignSelf: 'flex-start'}}>
            Alamat :
          </Text>
          <TextInput
            style={{
              borderWidth: 1,
              width: '100%',
              fontSize: 15,
              borderRadius: 10,
              marginBottom: 5,
            }}
            value={Alamat}
          />
        </View>
      </ScrollView>
      <View
        style={{
          width: '100%',
          backgroundColor: Yellow,
          height: 64,
          elevation: 10,
          borderColor: Black,
          borderBottomWidth: 1,
          alignItems: 'center',
          justifyContent: 'center',
          position: 'absolute',
          top: 0,
        }}>
        <TouchableOpacity
          onPress={() => navigation.goBack()}
          style={{
            position: 'absolute',
            left: 0,
            padding: 10,
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Image
            source={back}
            style={{width: 40, height: 40}}
            resizeMethod="resize"
            resizeMode="contain"
          />
        </TouchableOpacity>
        <Image
          source={logo_long}
          style={{width: '50%', height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
      </View>
    </SafeAreaView>
  );
};
export default AkunSaya;
