import AsyncStorage from '@react-native-async-storage/async-storage';
import React, {useState} from 'react';
import {
  SafeAreaView,
  Text,
  TextInput,
  View,
  StyleSheet,
  TouchableOpacity,
  Modal,
  ScrollView,
  Image,
  Platform,
} from 'react-native';
import {logo} from '../assets';
import {Login} from '../config/api';
import {White, Yellow} from '../styles/style';

const LoginScreen = ({navigation}) => {
  const [Email, setEmail] = useState('');
  const [Password, setPassword] = useState('');
  const [ShowPassword, setShowPassword] = useState(false);

  // modal
  const [Message, setMessage] = useState('Info');
  const [ShowModal, setShowModal] = useState(false);

  const ReqLogin = async () => {
    if (Email === '' || Password === '') {
      setShowModal(true);
      setMessage('Email dan Password harus disi');
      return;
    }
    setShowModal(true);
    setMessage('Loading...');
    await Login(Email, Password)
      .then(async (res) => {
        console.log(res.data);
        if (res.data.err_code === '00') {
          await AsyncStorage.setItem('profile', JSON.stringify(res.data.data));
          if (res.data.data.level === '1') {
            navigation.replace('HomePelanggan');
          } else {
            navigation.replace('HomeAdmin');
          }
          setMessage('Sukses');
        } else {
          setMessage(res.data.msg);
        }
      })
      .catch((err) => {
        console.log(err.response);
        setMessage('Gagal konek ke server');
      });
  };

  return (
    <ScrollView style={{backgroundColor: Yellow, flex: 1}}>
      <SafeAreaView style={styles.background}>
        <View style={styles.header}>
          <Image source={logo} style={{width: 200, height: 200}} />
          <Text style={styles.textTitle}>LOGIN</Text>
        </View>
        <Modal visible={ShowModal} animationType="fade" transparent={true}>
          <TouchableOpacity
            onPress={() => setShowModal(false)}
            style={{
              flex: 1,
              alignItems: 'center',
              justifyContent: 'center',
            }}>
            <View
              style={{
                backgroundColor: White,
                elevation: 10,
                borderRadius: 10,
                padding: 20,
                shadowColor: 'grey',
                shadowOffset: {width: 1, height: 1},
              }}>
              <Text style={{fontWeight: 'bold'}}>{Message}</Text>
            </View>
          </TouchableOpacity>
        </Modal>
        <View style={styles.inputForm}>
          <TextInput
            placeholder="Email"
            value={Email}
            style={{padding: 10, borderRadius: 10}}
            keyboardType="email-address"
            onChangeText={(val) => setEmail(val)}
          />
        </View>
        <View style={styles.inputForm}>
          <TextInput
            placeholder="Password"
            secureTextEntry={!ShowPassword}
            value={Password}
            style={{padding: 10, borderRadius: 10}}
            onChangeText={(val) => setPassword(val)}
          />
          <TouchableOpacity
            style={styles.btnhide}
            onPress={() => setShowPassword(!ShowPassword)}>
            <Text style={styles.colorGrey}>
              {!ShowPassword ? 'show' : 'hide'}
            </Text>
          </TouchableOpacity>
        </View>
        <TouchableOpacity style={styles.button} onPress={() => ReqLogin()}>
          <Text style={styles.textWhite}>MASUK</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.button2}
          onPress={() => navigation.replace('HomeSkip')}>
          <Text style={{color: 'black'}}>SKIP LOGIN</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.center}
          onPress={() => navigation.navigate('Register')}>
          <Text>Belum punya akun ?</Text>
          <Text style={styles.textTitle}>REGISTER</Text>
        </TouchableOpacity>
      </SafeAreaView>
    </ScrollView>
  );
};
export default LoginScreen;
const styles = StyleSheet.create({
  background: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: Yellow,
  },
  inputForm: {
    borderWidth: 1,
    borderRadius: 10,
    marginVertical: 3,
    width: '80%',
    maxWidth: Platform.OS === 'web' ? 500 : '80%',
  },
  button: {
    backgroundColor: 'black',
    padding: 10,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 5,
    marginVertical: 5,
  },
  button2: {
    backgroundColor: Yellow,
    padding: 10,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 5,
    marginVertical: 5,
    borderWidth: 1,
  },
  textWhite: {
    color: 'white',
  },
  textTitle: {
    fontWeight: 'bold',
  },
  header: {
    padding: 20,
    alignItems: 'center',
  },
  center: {
    alignItems: 'center',
  },
  btnhide: {position: 'absolute', right: 10, top: 10},
  colorGrey: {color: 'grey'},
});
