import React, {useState} from 'react';
import {
  SafeAreaView,
  Text,
  TextInput,
  View,
  StyleSheet,
  TouchableOpacity,
  Modal,
  Picker,
  Platform,
  Image,
  ScrollView,
} from 'react-native';
import {Register} from '../config/api';
import {White, Yellow} from '../styles/style';
// import {Picker} from '@react-native-community/picker';
import DateTimePicker from '@react-native-community/datetimepicker';
import moment from 'moment';
import {logo} from '../assets';

const RegisterScreen = ({navigation}) => {
  const [Email, setEmail] = useState('');
  const [Password, setPassword] = useState('');
  const [NoHp, setNoHp] = useState('');
  const [Nama, setNama] = useState('');
  const [JenisK, setJenisK] = useState('');
  const [TglLahir, setTglLahir] = useState(new Date());
  const [TglLahirDD, setTglLahirDD] = useState('');
  const [TglLahirMM, setTglLahirMM] = useState('');
  const [TglLahirYYYY, setTglLahirYYYY] = useState('');
  const [ShowPassword, setShowPassword] = useState(false);
  const [ShowDate, setShowDate] = useState(false);

  // modal
  const [Message, setMessage] = useState('Info');
  const [ShowModal, setShowModal] = useState(false);

  const ReqRegister = async () => {
    setShowModal(true);
    setMessage('Loading...');
    if (Nama == '') {
      setMessage('Nama harus diisi');
      return;
    }
    if (NoHp == '') {
      setMessage('No Hp harus diisi');
      return;
    }
    if (JenisK == '') {
      setMessage('Jenis Kelamin harus diisi');
      return;
    }
    if (Email == '') {
      setMessage('Email harus diisi');
      return;
    }

    let tanggal = '';

    if (Platform.OS === 'web') {
      if (TglLahirYYYY == '' || TglLahirMM == '' || TglLahirDD == '') {
        setMessage('Tanggal Lahir harus diisi');
        return;
      }
      tanggal = TglLahirYYYY + '-' + TglLahirMM + '-' + TglLahirDD;
    } else {
      tanggal = moment(TglLahir).format('YYYY-MM-DD');
    }

    if (Password == '') {
      setMessage('Password harus diisi');
      return;
    }

    let data = new FormData();
    data.append('email', Email);
    data.append('password', Password);
    data.append('nohp', NoHp);
    data.append('nama', Nama);
    data.append('jenis_kelamin', JenisK);
    data.append('tgl_lahir', tanggal);
    data.append('level', 1);
    await Register(data)
      .then((res) => {
        console.log(res.data);
        if (res.data.err_code === '00') {
          setMessage('Sukses Mendaftar, silakan login');
          // navigation.navigate('Login');
        } else {
          setMessage(res.data.msg);
        }
      })
      .catch((err) => {
        console.log(err.response);
        setMessage('Gagal konek ke server');
      });
  };

  const onChangeDate = (event, selectedDate) => {
    const currentDate = selectedDate || TglLahir;
    setShowDate(Platform.OS === 'ios');
    setTglLahir(currentDate);
  };

  return (
    <ScrollView style={{backgroundColor: Yellow, flex: 1}}>
      <SafeAreaView style={styles.background}>
        <View style={styles.header}>
          <Image source={logo} style={{width: 200, height: 200}} />
          <Text style={styles.textTitle}>REGISTER</Text>
        </View>
        <Modal visible={ShowModal} animationType="fade" transparent={true}>
          <TouchableOpacity
            onPress={() => setShowModal(false)}
            style={{
              flex: 1,
              alignItems: 'center',
              justifyContent: 'center',
            }}>
            <View
              style={{
                backgroundColor: White,
                elevation: 10,
                borderRadius: 10,
                padding: 20,
                shadowColor: 'grey',
                shadowOffset: {width: 1, height: 1},
              }}>
              <Text style={{fontWeight: 'bold'}}>{Message}</Text>
            </View>
          </TouchableOpacity>
        </Modal>
        <View style={styles.inputForm}>
          <TextInput
            placeholder="Nama Lengkap"
            value={Nama}
            style={{padding: 10, borderRadius: 10}}
            onChangeText={(val) => setNama(val)}
          />
        </View>
        <View style={styles.inputForm}>
          <TextInput
            placeholder="No HP (Whatsapp)"
            value={NoHp}
            keyboardType="phone-pad"
            style={{padding: 10, borderRadius: 10}}
            onChangeText={(val) => setNoHp(val)}
          />
        </View>
        <View
          style={{
            width: '80%',
            alignItems: 'center',
            borderWidth: Platform.OS !== 'web' ? 1 : 0,
            borderRadius: 10,
          }}>
          <Picker
            selectedValue={JenisK}
            style={{
              width: '100%',
              padding: 10,
              borderRadius: 10,
              borderColor: 'black',
              backgroundColor: Yellow,
              maxWidth: Platform.OS === 'web' ? 500 : '80%',
              marginVertical: 3,
            }}
            onValueChange={(val) => setJenisK(val)}>
            <Picker.Item key="0" value="" label="Jenis Kelamin" />
            <Picker.Item key="1" value="Laki - Laki" label="Laki - Laki" />
            <Picker.Item key="2" value="Perempuan" label="Perempuan" />
          </Picker>
        </View>
        <View style={styles.inputForm}>
          <TextInput
            placeholder="Email"
            value={Email}
            style={{padding: 10, borderRadius: 10}}
            keyboardType="email-address"
            onChangeText={(val) => setEmail(val)}
          />
        </View>
        {Platform.OS !== 'web' ? (
          <View style={styles.inputForm}>
            <TouchableOpacity
              onPress={() => setShowDate(!ShowDate)}
              style={{width: '100%', padding: 10, justifyContent: 'center'}}>
              <Text>{moment(TglLahir).format('DD-MM-YYYY')}</Text>
            </TouchableOpacity>
            {ShowDate && (
              <DateTimePicker
                testID="dateTimePicker"
                maximumDate={new Date()}
                value={TglLahir}
                mode="date"
                onChange={onChangeDate}
              />
            )}
          </View>
        ) : (
          <View style={[styles.inputForm, {flexDirection: 'row'}]}>
            <Text style={{padding: 10, width: '40%'}}>Tanggal Lahir : </Text>
            <TextInput
              placeholder="DD"
              value={TglLahirDD}
              maxLength={2}
              keyboardType="numeric"
              style={{padding: 10, width: '20%'}}
              onChangeText={(val) => setTglLahirDD(val)}
            />
            <TextInput
              placeholder="MM"
              value={TglLahirMM}
              maxLength={2}
              keyboardType="numeric"
              style={{padding: 10, width: '20%'}}
              onChangeText={(val) => setTglLahirMM(val)}
            />
            <TextInput
              placeholder="YYYY"
              value={TglLahirYYYY}
              maxLength={4}
              keyboardType="numeric"
              style={{padding: 10, width: '20%'}}
              onChangeText={(val) => setTglLahirYYYY(val)}
            />
          </View>
        )}
        <View style={styles.inputForm}>
          <TextInput
            placeholder="Password"
            style={{padding: 10, borderRadius: 10}}
            secureTextEntry={!ShowPassword}
            value={Password}
            onChangeText={(val) => setPassword(val)}
          />
          <TouchableOpacity
            style={styles.btnhide}
            onPress={() => setShowPassword(!ShowPassword)}>
            <Text style={styles.colorGrey}>
              {!ShowPassword ? 'show' : 'hide'}
            </Text>
          </TouchableOpacity>
        </View>
        <TouchableOpacity style={styles.button} onPress={() => ReqRegister()}>
          <Text style={styles.textWhite}>DAFTAR</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.center}
          onPress={() => navigation.navigate('Login')}>
          <Text>Sudah punya akun ?</Text>
          <Text style={styles.textTitle}>LOGIN</Text>
        </TouchableOpacity>
      </SafeAreaView>
    </ScrollView>
  );
};
export default RegisterScreen;
const styles = StyleSheet.create({
  background: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: Yellow,
  },
  inputForm: {
    borderWidth: 1,
    borderRadius: 10,
    marginVertical: 3,
    width: '80%',
    maxWidth: Platform.OS === 'web' ? 500 : '80%',
  },
  button: {
    backgroundColor: 'black',
    padding: 10,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 5,
    marginVertical: 5,
  },
  textWhite: {
    color: 'white',
  },
  textTitle: {
    fontWeight: 'bold',
  },
  header: {
    padding: 20,
    alignItems: 'center',
  },
  center: {
    alignItems: 'center',
  },
  btnhide: {position: 'absolute', right: 10, top: 10},
  colorGrey: {color: 'grey'},
});
