/* eslint-disable react-hooks/exhaustive-deps */
import React, {useState, useEffect} from 'react';
import {
  SafeAreaView,
  TouchableOpacity,
  Text,
  ScrollView,
  View,
  Image,
  TextInput,
  Picker,
  FlatList,
  Linking,
  Share,
  Alert,
} from 'react-native';
import {logo_long, search, back, filter, minus, cs_wa, share} from '../assets';
import {DOMAIN, GetProduk, URLPHOTO, URLWA} from '../config/api';
import {Yellow, White, Black, Grey} from '../styles/style';
import {WARNA_PUTIH} from '../utils/constant';
import {formatRupiah} from '../utils/GlobalFunctions';

const ProductDetail = ({navigation, route}) => {
  const [Data, setData] = useState({});
  const [Photo, setPhoto] = useState('');

  const GetProdukById = async (id) => {
    let formData = new FormData();
    formData.append('id_produk', id);
    await GetProduk(formData)
      .then((res) => {
        if (res.data.err_code === '00') {
          setData(res.data.data[0]);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const onShare = async () => {
    try {
      const result = await Share.share({
        message: DOMAIN + '/product/' + Data.id_produk,
      });
      if (result.action === Share.sharedAction) {
        if (result.activityType) {
          // shared with activity type of result.activityType
        } else {
          // shared
        }
      } else if (result.action === Share.dismissedAction) {
        // dismissed
      }
    } catch (error) {
      alert(error.message);
    }
  };

  useEffect(() => {
    let {key} = route.params;
    GetProdukById(key);
  }, [route.params]);

  return (
    <SafeAreaView style={{flex: 1, backgroundColor: WARNA_PUTIH}}>
      <ScrollView style={{flex: 1}}>
        <SafeAreaView style={{width: '100%'}}>
          <View
            style={{
              flex: 1,
              // position: 'absolute',
              paddingTop: 64,
              width: '100%',
              alignItems: 'center',
            }}>
            <Image
              source={{uri: URLPHOTO + Data.photo_produk}}
              style={{height: 200, width: '80%'}}
              resizeMethod="resize"
              resizeMode="contain"
            />
            <Text style={{fontSize: 20, fontWeight: 'bold'}}>
              {Data.produk}
            </Text>
            <Text style={{fontSize: 18, fontWeight: 'bold', marginBottom: 10}}>
              Harga {formatRupiah(Data.harga, 'Rp')}
            </Text>
            <Text style={{fontSize: 18, marginBottom: 10}}>
              Stok : {Data.stok} pcs
            </Text>
            <Text style={{fontSize: 20, fontWeight: 'bold'}}>Deskripsi</Text>
            <View style={{width: '100%', padding: 10}}>
              <Text style={{fontSize: 15}}>{Data.deskripsi_produk}</Text>
            </View>
          </View>
        </SafeAreaView>
      </ScrollView>
      <View
        style={{
          width: '100%',
          backgroundColor: Yellow,
          height: 64,
          elevation: 10,
          borderColor: Black,
          borderBottomWidth: 1,
          alignItems: 'center',
          justifyContent: 'center',
          position: 'absolute',
          top: 0,
        }}>
        <TouchableOpacity
          onPress={() => {
            if (navigation.canGoBack()) {
              navigation.goBack();
            } else {
              navigation.navigate('Splash');
            }
          }}
          style={{
            position: 'absolute',
            left: 0,
            padding: 10,
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Image
            source={back}
            style={{width: 40, height: 40}}
            resizeMethod="resize"
            resizeMode="contain"
          />
        </TouchableOpacity>
        <Image
          source={logo_long}
          style={{width: '50%', height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
      </View>
      <TouchableOpacity
        onPress={() => onShare()}
        style={{
          position: 'absolute',
          bottom: 10,
          right: 10,
          alignItems: 'center',
          justifyContent: 'center',
          backgroundColor: White,
        }}>
        <Image
          source={share}
          style={{width: 50, height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() =>
          Linking.openURL(
            URLWA +
              Data.produk +
              ' /n ' +
              DOMAIN +
              '/product/' +
              Data.id_produk,
          )
        }
        style={{
          position: 'absolute',
          bottom: 70,
          right: 10,
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <Image
          source={cs_wa}
          style={{width: 50, height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
      </TouchableOpacity>
    </SafeAreaView>
  );
};
export default ProductDetail;
