/* eslint-disable react-hooks/exhaustive-deps */
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, {useState, useEffect} from 'react';
import {
  SafeAreaView,
  TouchableOpacity,
  Text,
  ScrollView,
  RefreshControl,
  View,
  Image,
  TextInput,
  Picker,
  FlatList,
  Platform,
} from 'react-native';
import {logo_long, search, back, filter, minus} from '../assets';
import {GetProduk, Register, URLPHOTO} from '../config/api';
import {Yellow, White, Black, Grey} from '../styles/style';
import {WARNA_PUTIH} from '../utils/constant';
import {formatRupiah} from '../utils/GlobalFunctions';

const ListProduct = ({navigation, route}) => {
  const [DataProduk, setDataProduk] = useState([]);
  const [SelKategori, setSelKategori] = useState('');
  const [SelToko, setSelToko] = useState('');
  const [ShowFilter, setShowFilter] = useState(false);
  const [DataKategori, setDataKategori] = useState([]);
  const [Loading, setLoading] = useState(false);
  const [searchData, setSearchData] = useState('');
  const [Profile, setProfile] = useState(null);

  const ReqGetProduct = async (
    id_kategori = SelKategori,
    id_toko = SelToko,
    searchD = searchData,
  ) => {
    setLoading(true);
    let formData = new FormData();
    formData.append('id_toko', id_toko);
    formData.append('id_kategori', id_kategori);
    formData.append('search', searchD);
    await GetProduk(formData)
      .then((res) => {
        setLoading(false);
        console.log(res.data);
        if (res.data.err_code == '00') {
          setDataProduk(res.data.data);
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  };

  const getProfil = async () => {
    let profile = await AsyncStorage.getItem('profile');
    if (profile != null) {
      setProfile(profile);
    }
  };

  useEffect(() => {
    let {id_kategori, data_kategori, id_toko} = route.params;
    setDataKategori(data_kategori);
    setSelToko(id_toko);
    setSelKategori(id_kategori);
    ReqGetProduct(id_kategori, id_toko);
    getProfil();
  }, [route.params]);

  return (
    <SafeAreaView style={{flex: 1, backgroundColor: WARNA_PUTIH}}>
      <ScrollView style={{flex: 1}}>
        <SafeAreaView style={{width: '100%'}}>
          <View
            style={{
              flex: 1,
              // position: 'absolute',
              paddingTop: 64,
              width: '100%',
              alignItems: 'center',
            }}>
            <FlatList
              data={DataProduk}
              keyExtractor={(item, index) => index.toString()}
              onRefresh={() => ReqGetProduct(SelKategori, SelToko, searchData)}
              refreshing={Loading}
              // scrollEnabled={false}
              style={{
                width: '100%',
                padding: 5,
              }}
              contentContainerStyle={{alignItems: 'center', paddingBottom: 20}}
              numColumns={2}
              renderItem={({item}) => (
                <TouchableOpacity
                  onPress={() =>
                    navigation.push('ProductDetail', {key: item.id_produk})
                  }
                  style={{
                    alignItems: 'center',
                    alignSelf: 'center',
                    margin: 2,
                    elevation: 10,
                    shadowColor: Grey,
                    shadowOffset: {width: 1, height: 1},
                    borderRadius: 10,
                    shadowOpacity: 0.9,
                    padding: 5,
                    backgroundColor: WARNA_PUTIH,
                  }}>
                  <Image
                    source={{uri: URLPHOTO + item.photo_produk}}
                    style={{height: 200, width: 160}}
                    resizeMethod="resize"
                  />
                  <Text style={{fontSize: 15}}>{item.produk}</Text>
                  <Text style={{fontWeight: 'bold'}}>
                    {formatRupiah(item.harga, 'Rp')}
                  </Text>
                </TouchableOpacity>
              )}
            />
            {Loading && Platform.OS === 'web' && (
              <Text style={{fontWeight: 'bold'}}>Loading ....</Text>
            )}
            {DataProduk.length === 0 && !Loading && (
              <Text style={{fontWeight: 'bold'}}>Data Kosong</Text>
            )}
          </View>
        </SafeAreaView>
      </ScrollView>
      <View
        style={{
          width: '100%',
          backgroundColor: Yellow,
          height: 64,
          elevation: 10,
          borderColor: Black,
          borderBottomWidth: 1,
          alignItems: 'center',
          justifyContent: 'center',
          position: 'absolute',
          top: 0,
        }}>
        <TouchableOpacity
          onPress={() => navigation.goBack()}
          style={{
            position: 'absolute',
            left: 0,
            padding: 10,
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Image
            source={back}
            style={{width: 40, height: 40}}
            resizeMethod="resize"
            resizeMode="contain"
          />
        </TouchableOpacity>
        <Image
          source={logo_long}
          style={{width: '50%', height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
        <TouchableOpacity
          onPress={() => setShowFilter(!ShowFilter)}
          style={{
            position: 'absolute',
            right: 0,
            padding: 10,
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Image
            source={filter}
            style={{width: 40, height: 40}}
            resizeMethod="resize"
            resizeMode="contain"
          />
        </TouchableOpacity>
      </View>
      {ShowFilter && (
        <View
          style={{
            flex: 1,
            position: 'absolute',
            top: 64,
            width: '100%',
            backgroundColor: Yellow,
            borderBottomWidth: 1,
          }}>
          <View style={{padding: 10, alignItems: 'center', width: '100%'}}>
            <Text style={{fontWeight: 'bold', fontSize: 20, paddingBottom: 5}}>
              Kategori
            </Text>
            <View
              style={{
                width: '80%',
                borderWidth: Platform.OS !== 'web' ? 1 : 0,
                borderRadius: 10,
              }}>
              <Picker
                selectedValue={SelKategori}
                onValueChange={(val) => {
                  setSelKategori(val);
                  ReqGetProduct(val, SelToko, searchData);
                }}
                style={{
                  width: '100%',
                  padding: 10,
                  borderRadius: 10,
                  borderColor: 'black',
                  backgroundColor: Yellow,
                }}>
                <Picker.Item key="0" value="" label="PILIH KATEGORI" />
                {DataKategori.map((kategori, i) => (
                  <Picker.Item
                    key={i}
                    value={kategori.id_kategori}
                    label={kategori.kategori}
                  />
                ))}
              </Picker>
            </View>
          </View>
          <View style={{padding: 10, alignItems: 'center', width: '100%'}}>
            <View
              style={{
                borderWidth: 1,
                borderRadius: 10,
                width: '80%',
                justifyContent: 'center',
              }}>
              <Image
                source={search}
                style={{
                  width: 30,
                  height: 30,
                  position: 'absolute',
                  left: 10,
                }}
              />
              <TextInput
                style={{
                  padding: 10,
                  paddingLeft: 40,
                  borderRadius: 10,
                  width: '100%',
                  alignSelf: 'flex-end',
                }}
                returnKeyType="search"
                value={searchData}
                placeholder="Cari Produk yang anda butuhkan "
                onKeyPress={() =>
                  ReqGetProduct(SelKategori, SelToko, searchData)
                }
                onEndEditing={() =>
                  ReqGetProduct(SelKategori, SelToko, searchData)
                }
                onChangeText={(val) => setSearchData(val)}
              />
            </View>
          </View>
          <View style={{alignItems: 'center', width: '100%'}}>
            <TouchableOpacity
              onPress={() => setShowFilter(!ShowFilter)}
              style={{
                right: 0,
                alignItems: 'center',
                justifyContent: 'center',
              }}>
              <Image
                source={minus}
                style={{width: 40, height: 40}}
                resizeMethod="resize"
                resizeMode="contain"
              />
            </TouchableOpacity>
          </View>
        </View>
      )}
    </SafeAreaView>
  );
};
export default ListProduct;
