/* eslint-disable react-hooks/exhaustive-deps */
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, {useState} from 'react';
import {useEffect} from 'react';
import {WARNA_PUTIH} from '../../utils/constant';
import {
  Platform,
  SafeAreaView,
  View,
  Text,
  TouchableOpacity,
  Image,
  StyleSheet,
  FlatList,
  Picker,
  ScrollView,
  TextInput,
  Linking,
} from 'react-native';
// import {SliderBox} from 'react-native-image-slider-box';
import Swiper from 'react-native-web-swiper';
import {
  logo_long,
  search,
  menu,
  cs_wa,
  dapur,
  cancel,
  minus,
} from '../../assets';
import {
  GetBanner,
  GetKategori,
  GetToko,
  URLPHOTO,
  URLWA,
} from '../../config/api';
import {Black, Grey, White, Yellow} from '../../styles/style';
import {useIsFocused} from '@react-navigation/native';

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: 'white',
  },
  slideContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    width: '100%',
    height: 220,
  },
  slide1: {
    backgroundColor: 'rgba(20,20,200,0.3)',
  },
  slide2: {
    backgroundColor: 'rgba(20,200,20,0.3)',
  },
  slide3: {
    backgroundColor: 'rgba(200,20,20,0.3)',
  },
  button2: {
    backgroundColor: Black,
    padding: 10,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 5,
    marginVertical: 5,
    borderWidth: 1,
    width: '50%',
    alignSelf: 'center',
  },
});

const HomeScreen = ({navigation}) => {
  const isFocused = useIsFocused();
  const [ShowMenu, setShowMenu] = useState(false);
  const [Banner, setBanner] = useState([]);
  const [Tokos, setTokos] = useState([]);
  const [SelToko, setSelToko] = useState('');
  const [Kategoris, setKategoris] = useState([]);
  const LogOut = async () => {
    await AsyncStorage.removeItem('profile');
    navigation.replace('Login');
  };

  const ReqGetBanner = async (idtoko = SelToko) => {
    if (idtoko === '') {
      idtoko = null;
    }
    await GetBanner(idtoko)
      .then((res) => {
        console.log(res.data);
        if (res.data.err_code == '00') {
          setBanner(res.data.data);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const ReqGetToko = async () => {
    await GetToko()
      .then((res) => {
        console.log(res.data);
        if (res.data.err_code == '00') {
          setTokos(res.data.data);
          if (res.data.data.length !== 0) {
            setSelToko(res.data.data[0].id_toko);
            ReqGetKategori(res.data.data[0].id_toko);
          }
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const ReqGetKategori = async (idToko = SelToko) => {
    if (idToko == '') {
      idToko = null;
    }
    await GetKategori(idToko)
      .then((res) => {
        console.log(res.data);
        if (res.data.err_code == '00') {
          setKategoris(res.data.data);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    ReqGetBanner();
    ReqGetToko();
  }, [isFocused]);

  const ListBanner = () => (
    <Swiper
      innerContainerStyle={{height: 220}}
      containerStyle={{height: 220, top: 64}}>
      <View style={styles.slideContainer}>
        <Image
          source={dapur}
          style={{width: '100%', height: 220}}
          resizeMethod="resize"
        />
      </View>
      {Banner.map((banner) => (
        <View style={styles.slideContainer}>
          <Image
            source={{uri: URLPHOTO + banner.photo_banner}}
            style={{width: '100%', height: 220}}
            resizeMethod="resize"
          />
        </View>
      ))}
    </Swiper>
  );

  return (
    <SafeAreaView style={{flex: 1, backgroundColor: WARNA_PUTIH}}>
      <ScrollView style={{flex: 1}}>
        <SafeAreaView style={{width: '100%'}}>
          <ListBanner />
          <View
            style={{
              flex: 1,
              position: Platform.OS === 'web' ? 'absolute' : 'relative',
              marginTop: Platform.OS === 'web' ? 284 : 44,
              width: '100%',
              minHeight: '100%',
            }}>
            <View style={{padding: 10, alignItems: 'center', width: '100%'}}>
              <Text style={{fontWeight: 'bold', margin: 20, fontSize: 20}}>
                TOKO
              </Text>
              <View
                style={{
                  borderWidth: Platform.OS !== 'web' ? 1 : 0,
                  width: '80%',
                  borderRadius: 10,
                }}>
                <Picker
                  selectedValue={SelToko}
                  onValueChange={(val) => {
                    setSelToko(val);
                    ReqGetKategori(val);
                    ReqGetBanner(val);
                  }}
                  style={{
                    width: '100%',
                    padding: 10,
                    borderRadius: 10,
                    borderColor: 'black',
                  }}>
                  <Picker.Item key="0" value="" label="PILIH TOKO" />
                  {Tokos.map((toko, i) => (
                    <Picker.Item
                      key={i}
                      value={toko.id_toko}
                      label={toko.nama_toko}
                    />
                  ))}
                </Picker>
              </View>
            </View>
            {/* <View style={{padding: 10, alignItems: 'center', width: '100%'}}>
              <View
                style={{
                  borderWidth: 1,
                  borderRadius: 10,
                  width: '80%',
                  justifyContent: 'center',
                }}>
                <Image
                  source={search}
                  style={{
                    width: 30,
                    height: 30,
                    position: 'absolute',
                    left: 10,
                  }}
                />
                <TextInput
                  style={{
                    padding: 10,
                    paddingLeft: 40,
                    borderRadius: 10,
                    width: '100%',
                    alignSelf: 'flex-end',
                  }}
                  placeholder="Cari Produk yang anda butuhkan "
                />
              </View>
            </View> */}
            <View style={{padding: 10, alignItems: 'center', width: '100%'}}>
              <Text style={{fontWeight: 'bold', margin: 20, fontSize: 20}}>
                KATEGORI
              </Text>
              <FlatList
                data={Kategoris}
                keyExtractor={(item, index) => index.toString()}
                horizontal={false}
                contentContainerStyle={{alignItems: 'center', padding: 4}}
                numColumns={2}
                scrollEnabled={false}
                renderItem={({item}) => (
                  <TouchableOpacity
                    onPress={() =>
                      navigation.navigate('ListProduct', {
                        id_kategori: item.id_kategori,
                        data_kategori: Kategoris,
                        id_toko: SelToko,
                      })
                    }
                    style={{
                      alignItems: 'center',
                      padding: 5,
                      margin: 3,
                      borderRadius: 10,
                      shadowColor: Grey,
                      backgroundColor: WARNA_PUTIH,
                      elevation: 5,
                      shadowOpacity: 0.9,
                      shadowOffset: {width: 1, height: 1},
                    }}>
                    <Image
                      source={{uri: URLPHOTO + item.photo_kategori}}
                      style={{height: 150, width: 150}}
                      resizeMethod="resize"
                    />
                    <Text>{item.kategori}</Text>
                  </TouchableOpacity>
                )}
              />
            </View>

            {Tokos.map((toko) => {
              if (toko.id_toko === SelToko) {
                return (
                  <View
                    style={{
                      padding: 10,
                      alignItems: 'center',
                      width: '100%',
                      paddingVertical: 10,
                      backgroundColor: Yellow,
                      borderTopWidth: 1,
                    }}>
                    <Text
                      style={{fontSize: 20, fontWeight: 'bold', color: Black}}>
                      {toko.nama_toko}
                    </Text>
                    <Text style={{color: Black}}>Alamat</Text>
                    <Text style={{color: Black}}>{toko.alamat_toko}</Text>
                  </View>
                );
              }
            })}
          </View>
        </SafeAreaView>
      </ScrollView>
      <View
        style={{
          width: '100%',
          backgroundColor: Yellow,
          height: 64,
          elevation: 10,
          borderColor: Black,
          borderBottomWidth: 1,
          alignItems: 'center',
          justifyContent: 'center',
          position: 'absolute',
          top: 0,
        }}>
        <TouchableOpacity
          onPress={() => setShowMenu(!ShowMenu)}
          style={{
            position: 'absolute',
            left: 0,
            padding: 10,
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Image
            source={ShowMenu ? cancel : menu}
            style={{width: 40, height: 40}}
            resizeMethod="resize"
            resizeMode="contain"
          />
        </TouchableOpacity>
        <Image
          source={logo_long}
          style={{width: '50%', height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
      </View>
      <TouchableOpacity
        onPress={() => Linking.openURL(URLWA)}
        style={{
          position: 'absolute',
          bottom: 10,
          right: 10,
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <Image
          source={cs_wa}
          style={{width: 50, height: 50}}
          resizeMethod="resize"
          resizeMode="contain"
        />
      </TouchableOpacity>
      {ShowMenu && (
        <View
          style={{
            position: 'absolute',
            top: 64,
            width: '100%',
            backgroundColor: Yellow,
            borderBottomWidth: 1,
            paddingTop: 10,
          }}>
          <TouchableOpacity style={styles.button2} onPress={() => LogOut()}>
            <Text style={{color: White}}>LOGIN</Text>
          </TouchableOpacity>
          <View style={{alignItems: 'center', width: '100%'}}>
            <TouchableOpacity
              onPress={() => setShowMenu(!ShowMenu)}
              style={{
                right: 0,
                alignItems: 'center',
                justifyContent: 'center',
              }}>
              <Image
                source={minus}
                style={{width: 40, height: 40}}
                resizeMethod="resize"
                resizeMode="contain"
              />
            </TouchableOpacity>
          </View>
        </View>
      )}
    </SafeAreaView>
  );
};
export default HomeScreen;
