import React from 'react';
import {createStackNavigator} from '@react-navigation/stack';
// import {createDrawerNavigator} from '@react-navigation/drawer';
// const Drawer = createDrawerNavigator();
const Stack = createStackNavigator();
import SplashScreen from './screens/SplashScreen';
import LoginScreen from './screens/LoginScreen';
import RegisterScreen from './screens/RegisterScreen';
import HomeAdmin from './screens/admin/HomeScreen';
import HomePelanggan from './screens/pelanggan/HomeScreen';
import HomeSkip from './screens/skiplogin/HomeScreen';
import ListProduct from './screens/ListProductScreen';
import ProductDetail from './screens/ProductDetailScreen';
import AkunSaya from './screens/pelanggan/AkunSayaScreen';
import Keranjang from './screens/pelanggan/KeranjangScreen';
import Riwayat from './screens/pelanggan/RiwayatScreen';
// const DrawerSkip = () => (
//   <Drawer.Navigator initialRouteName="HomeSkip">
//     <Drawer.Screen name="HomeSkip" component={HomeSkip} />
//   </Drawer.Navigator>
// );

export const config = {
  screens: {
    ListProduct: 'product',
    ProductDetail: 'product/:key',
    Login: 'login',
    Register: 'register',
    HomeSkip: 'home',
  },
};

const Router = () => {
  return (
    <Stack.Navigator initialRouteName="Splash">
      <Stack.Screen
        name="Splash"
        component={SplashScreen}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="Login"
        component={LoginScreen}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="Register"
        component={RegisterScreen}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="HomeAdmin"
        component={HomeAdmin}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="HomePelanggan"
        component={HomePelanggan}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="HomeSkip"
        component={HomeSkip}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="ListProduct"
        component={ListProduct}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="ProductDetail"
        component={ProductDetail}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="AkunSaya"
        component={AkunSaya}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="Keranjang"
        component={Keranjang}
        options={{headerShown: false}}
      />
      <Stack.Screen
        name="Riwayat"
        component={Riwayat}
        options={{headerShown: false}}
      />
    </Stack.Navigator>
  );
};
export default Router;
